import React, {Component} from 'react';
import {TempIo} from "./IO/TempIo";
import ReactApexChart from "react-apexcharts";

interface Props {

}

interface State {
    currentVale: number[];
}

class TempComponent extends Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {currentVale: []}
        this.tempio = new TempIo();
    }

    public tempio: TempIo;

    async componentDidMount() {
        setInterval(async () => {
            const data = await this.tempio.Fetch();
            if (data.status === 200) {
                if (data.data.value > 0) {
                    this.setState({currentVale: [...this.state.currentVale, data.data.value]})
                }
            }
        }, 1000);
    }

    render() {
        return (
            <div>
                {this.state.currentVale.length > 0 &&

                <ReactApexChart options={

                    {
                        chart: {
                            height: 350,
                            type: 'area'
                        },
                        dataLabels: {
                            enabled: false
                        },
                        stroke: {
                            curve: 'smooth'
                        },

                    }


                } series={
                    [{
                        name: 'series1',
                        data: this.state.currentVale
                    }]
                } type="area" height={350}/>


                }


            </div>
        );
    }
}

export default TempComponent;